<template>
  <div class="search">
    <el-form>
      <el-form-item label="性别：">
        <el-checkbox-group v-model="genderSelect">
          <el-checkbox-button v-for="item in genders"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="职业：">
        <el-checkbox-group v-model="careerSelect">
          <el-checkbox-button v-for="item in careers"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>

      <el-form-item label="标签：">
        <el-checkbox-group v-model="tagSelect">
          <el-checkbox-button v-for="item in tags"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="星级：">
        <el-checkbox-group v-model="levelSelect">
          <el-checkbox-button v-for="item in levels"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="获取方式：">
        <el-button type="primary"
                   disabled>公开寻访</el-button>
        <!-- <el-checkbox-group v-model="acquisitionSelect">
          <el-checkbox-button v-for="item in acquisitions"
                              :label="item.value"
                              :key="item.value">{{item.label}}</el-checkbox-button>
        </el-checkbox-group> -->
      </el-form-item>
      <el-form-item>
        <el-button type="primary"
                   @click="onClear">清空</el-button>
      </el-form-item>
    </el-form>
    <el-collapse accordion>
      <el-collapse-item v-for="item in groupList"
                        :key="item.title"
                        :title="item.title">
        <div slot="title"
             class="collapse-title">
          <span>{{item.title}}</span>
          <i>{{item.list&&item.list.length}}</i>
          <div class="collapse-title-right">
            <span v-for="sItem in item.list"
                  :key="sItem.name">{{sItem.name+'、'}}</span>
          </div>
        </div>
        <el-table :data="item.list"
                  style="width:100%;padding: 20px">
          <el-table-column type="index">
          </el-table-column>
          <el-table-column prop="name"
                           label="姓名">
          </el-table-column>
          <el-table-column prop="genderLabel"
                           label="性别">
          </el-table-column>
          <el-table-column prop="careerLabel"
                           label="职业">
          </el-table-column>
          <el-table-column prop="level"
                           label="星级">
          </el-table-column>
          <el-table-column prop="tagLabel"
                           label="标签">
          </el-table-column>

          <el-table-column prop="acquisitionLabel"
                           label="获取途径">
          </el-table-column>

          <el-table-column prop="campLabel"
                           label="阵营">
          </el-table-column>
        </el-table>
      </el-collapse-item>
    </el-collapse>
  </div>
</template>
<script>
import { findAll } from '../api/role.js'
import { careers, camps, genders, tags, acquisitions } from '../utils/constant.js'
export default {
  name: 'role',
  data () {
    return {
      careers: careers(),
      tags: tags(),
      genders: genders(),
      camps: camps(),
      acquisitions: acquisitions(),
      levels: [{
        label: '资深干员',
        value: '5'
      }, {
        label: '高级资深干员',
        value: '6'
      }],
      careerSelect: [],
      tagSelect: [],
      genderSelect: [],
      roleList: [],
      levelSelect: [],
      acquisitionSelect: ['1'],
      allSelect: [],
      groupList: []
    }
  },
  watch: {
    careerSelect (val, oldVal) {
      this.handlerSelect('career', oldVal)
    },
    tagSelect (val, oldVal) {
      this.handlerSelect('tag', oldVal)
    },
    genderSelect (val, oldVal) {
      this.handlerSelect('gender', oldVal)
    },
    levelSelect (val, oldVal) {
      this.handlerSelect('level', oldVal)
    }
  },
  created () {
    this.init()
  },
  methods: {
    init () {
      findAll().then(res => {
        this.roleList = res.map(item => {
          item.tagLabel = this.formatLabel(item.tag, 'tag')
          item.acquisitionLabel = this.formatLabel(item.acquisition, 'acquisition')
          item.campLabel = this.formatLabel(item.camp, 'camp')
          item.careerLabel = this.formatLabel(item.career, 'career')
          item.genderLabel = this.formatLabel(item.gender, 'gender')
          return item
        })
      }).catch(() => {
        this.$message.error('粗错了')
      })
    },
    formatLabel (data, key) {
      if (!data) { return '' }
      if (typeof data === 'string') {
        data = [data]
      }
      return data.map(per => {
        return this.getLabel(key, per)
      }).join('、')
    },
    onClear () {
      this.careerSelect = []
      this.tagSelect = []
      this.genderSelect = []
      this.levelSelect = []
    },
    valiLength () {
      let length = this.careerSelect.length + this.tagSelect.length + this.genderSelect.length + this.levelSelect.length
      return length <= 5
    },
    handlerSelect (key, oldVal) {
      if (!this.valiLength()) {
        this.$message({
          message: '最多只能选择5个tag',
          type: 'warning'
        })
        this[key + 'Select'] = oldVal
        return
      }

      // this.allSelect = []
      this.allSelect = this.allSelect.filter((item) => {
        return item.key !== key
      }) || []
      this[key + 'Select'].forEach((item) => {
        this.allSelect.push({ key: key, value: item })
      })
      let length = this.allSelect.length
      this.groupList = []
      if (length > 0) {
        for (let a = 0; a < length; a++) {
          let dataa = [this.allSelect[a]]
          let lista = this.filterGroupList(dataa)
          if (lista.length > 0) {
            this.groupList.push({ title: this.formatGroupTitle(dataa), data: dataa, list: lista })
          }
          if (length > 1) {
            for (let b = a + 1; b < length; b++) {
              let datab = [this.allSelect[a], this.allSelect[b]]
              let listb = this.filterGroupList(datab)
              if (listb.length > 0) {
                this.groupList.push({ title: this.formatGroupTitle(datab), data: datab, list: listb })
              }
              if (length > 2) {
                for (let c = b + 1; c < length; c++) {
                  let datac = [this.allSelect[a], this.allSelect[b], this.allSelect[c]]
                  let listc = this.filterGroupList(datac)
                  if (listc.length > 0) {
                    this.groupList.push({ title: this.formatGroupTitle(datac), data: datac, list: listc })
                  }
                }
              }
            }
          }
        }
      }
      this.groupList.sort((a, b) => {
        return b.data.length - a.data.length || a.list.length - b.list.length
      })
    },
    formatGroupTitle (data) {
      let title = []
      data.forEach(item => {
        title.push(this.getLabel(item.key, item.value))
      })
      return title.join('、')
    },
    filterGroupList (data) {
      let arr = [...data, { key: 'acquisition', value: '1' }]
      let list = this.roleList.filter(item => {
        return arr.every(per => {
          if (Array.isArray(item[per.key])) {
            return item[per.key].includes(per.value)
          } else {
            return item[per.key] === per.value
          }
        })
      }).sort((a, b) => {
        return b.level - a.level
      })
      return list
    },
    getLabel (key, val) {
      let data = this[key + 's'].find(item => {
        return item.value === val
      }) || {}
      return data.label || ''
    }
  }
}
</script>
<style lang="scss" scope>
.search {
  text-align: left;
  padding: 20px;
  .collapse-title {
    width: 100%;
    display: flex;
    > i {
      margin: 0 15px 5px;
    }
    &-right {
      flex: 1;
      text-align: right;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
    }
  }
}
</style>
